body {
  background-color: #0e101c;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  margin: 0;
}

form {
  max-width: 800px;
  margin: 0 auto;
  background: #0e101c;
}

h1 {
  margin-top: 80px;
  font-weight: 100;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(79, 98, 148);
}

.form {
  background: #0e101c;
  max-width: 400px;
  margin: 0 auto;
}

button[type="button"] {
  background: black;
  border: 1px solid white;
}

p {
  color: #bf1650;
  text-align: center;
}

input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid white;
  padding: 10px 15px;
  margin-bottom: 10px;
  font-size: 14px;
}

label {
  line-height: 2;
  text-align: left;
  display: block;
  margin-bottom: 13px;
  margin-top: 20px;
  color: white;
  font-size: 14px;
  font-weight: 200;
}

button,
input[type="submit"] {
  background: #33ccff;
  color: black;
  text-transform: uppercase;
  border: none;
  margin-top: 40px;
  padding: 20px;
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 10px;
}

button {
  display: block;
  appearance: none;
  border-radius: 4px;
  width: 100%;
}

input[type="submit"]:hover {
  background: #0099ff;
}

input[type="button"]:active,
input[type="submit"]:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}

input:disabled {
  opacity: 0.4;
}

input[type="button"]:hover {
  transition: 0.3s all;
}

input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
}

.App {
  max-width: 600px;
  margin: 0 auto;
}

form {
  margin-top: 50px;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.counter {
  font-weight: 400;
  background: white;
  color: black;
  padding: 10px 15px;
  border-radius: 4px;
  position: fixed;
  top: 20px;
  right: 20px;
}
.ant-carousel {
  display: block;
  text-align: center;
  height: 93vh;
  line-height: 50vh;
  background: #364d79;
  overflow: hidden;
}

.slick-slider {
  height: 93vh;
}
.slick-list {
  height: 93vh;
}
.navbar {
  height: 7vh;
  background-color: #0e101c;
}
.nav-link {
  color: aliceblue;
  text-decoration: none;
}
.card {
  background-color: #99ebff;

  border-radius: 5%;
  font-size: 20px;
  font-family: "Courier New", Courier, monospace;
  padding: 2vw;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5vh;
  margin-bottom: 5vh;
  width: 80%;
}
.card-text {
  color: black;
  text-align: justify;
}
.card-title {
  color: black;
  font-weight: bold;
}
